

#container {
  display: block;
  margin: 0 auto;
  height: 30vh;
  position: relative;
  background-color: #87CEEB;
  top: -20px;
  width: 225px;
}

#container div { position: absolute; }

#beaker {
  border: 12px solid white;
  border-top: 0;
  border-radius: 0 0 40px 40px;
  height: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  bottom: 0;
  width: 100%;
}

#liquid {
  background-color: #edaf32;
  border: 10px solid #edaf32;
  border-radius: 0 0 30px 30px;
  bottom: -1px;
  overflow: hidden;
  width: 100%;
}
#liquid:after {
  background-color: rgba(255, 255, 255, 0.25);
  content: '';
  height: 100%;
  border-radius: 0 0 20px 20px;
  position: absolute;
  width: 50%;
}

.bubble {
  background-color: rgba(255, 255, 255, 0.2);
  bottom: 0;
  border-radius: 50%;
  height: 20px;
  width: 20px;
}


/* Foam */

.beer-foam {
  width:100%;
  bottom: -5%;
}

.foam-1, .foam-2, .foam-3, .foam-4,
.foam-5, .foam-6, .foam-7 {
  position: absolute;
  z-index: 999;
  width:23%;
  bottom: 0;
  height: 16%;
  border-radius: 30px;
  background-color: whitesmoke;
}
.foam-1 {
  top: -30px;
}
.foam-2 {
  top: -35px;
  left: 14%;

}
.foam-3 {
  top: -25px;
  left: 27%;

}
.foam-4 {
  top: -35px;
  left:40%

}
.foam-5 {
  top: -30px;
  left: 53%;

}
.foam-6 {
  top: -20px;
  left: 67%;

}
.foam-7 {
  top: -30px;
  left: 85%

}



